body {
  background-color: #607d8b;
  width: 100%;
  height: 300vh;
  margin: 0;
  padding: 0; }

#app {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.reveal {
  width: 400px;
  height: 300px;
  overflow: hidden;
  position: relative; }

.contentlayer {
  width: 100%;
  height: 100%;
  background: url("https://images.unsplash.com/photo-1542137722061-efd1cbdf156c?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=69b9754011a0840c859142eaa6c153ba&auto=format&fit=crop&w=1500&q=80");
  background-size: cover;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  transform: translateX(-100%); }
  .contentlayer.visible {
    animation-name: contentAnimateLeft;
    animation-duration: 800ms;
    animation-timing-function: ease-out;
    animation-fill-mode: forwards; }

.colorlayer {
  width: 100%;
  height: 100%;
  background-color: #000;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  transform: translateX(-100%); }
  .colorlayer.visible {
    animation-name: colorAnimateLeft;
    animation-duration: 1200ms;
    animation-fill-mode: forwards;
    animation-timing-function: ease-out; }

.container.visible {
  animation-name: containerAnimateLeft;
  animation-duration: 600ms;
  animation-fill-mode: forwards;
  animation-timing-function: ease-out; }

@keyframes containerAnimateLeft {
  0% {
    transform: translateX(-40px); }
  100% {
    transform: translateX(0%); } }

@keyframes contentAnimateLeft {
  0% {
    transform: translateX(-50%); }
  100% {
    transform: translateX(0%); } }

@keyframes colorAnimateLeft {
  0% {
    width: 0%;
    left: 0;
    opacity: 0; }
  50% {
    opacity: 1;
    left: 100%;
    width: 100%; }
  100% {
    left: 100%;
    width: 0%; } }
